body {
  margin: 0;
  padding: 25px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  display: flex;
}

.people {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px 20px;
  max-width: 500px;
  margin: 0 auto;
  display: flex;
}

@media (width >= 1000px) {
  .people {
    justify-content: start;
    max-width: 1000px;
  }
}

.person {
  background-color: #fff;
  border-radius: 10px;
  width: 200px;
  height: 231px;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person:hover .extra {
  background-color: #eee;
  justify-content: center;
  min-width: 130px;
  margin: 9px auto 0;
  font-weight: 500;
  display: flex;
}

.person:hover .photo img {
  border-radius: 60px;
}

.photo {
  position: relative;
}

.photo img {
  transform: inherit;
  border-radius: 200px;
  width: 200px;
  height: 200px;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 200px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 18px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.details {
  flex-wrap: wrap;
  margin: 8px auto 0;
  display: flex;
}

.name {
  color: #2f4f4f;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-size: 20px;
}

.extra {
  background-color: #eee;
  display: none;
}

.sport {
  font-size: 18px;
}

.sport:after {
  content: ",";
}

.pay {
  margin-left: 5px;
  font-size: 18px;
}

.pay:before {
  content: "$";
}

.pay:after {
  content: "M";
}
